	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"How I Punked Facebook","date":"10 Feb 2015","slug":"/2015-02-10-how-i-punked-facebook/","tags":"Y2015"},"html":"<p>It took me nearly two years, but I finally did it. I was able to get the better of Facebook. Yesterday I was able to celebrate my birthday without a single greeting on social media, even if my wife very nearly <a href=\"https://www.facebook.com/papascott/posts/10152947844446281y\">gave me away</a>.</p>\n<p>It was heaven! It was bliss! I was living free of the distractions of the digital world, if only for one day. My day! For me, me alone!</p>\n<p>I hope your day was a good one too.</p>"},"next":{"fields":{"title":"Where Were You?","date":"09 Nov 2014","slug":"/2014-11-09-where-were-you/"}},"previous":{"fields":{"title":"Confirmation","date":"20 Apr 2015","slug":"/2015-04-20-confirmation/"}}},{"node":{"fields":{"title":"Where Were You?","date":"09 Nov 2014","slug":"/2014-11-09-where-were-you/","tags":"Y2014"},"html":"<p>Where was I 25 years ago as the Berlin Wall fell? I must confess, I don't remember exactly. If I had known the news would radically change our lives, I might have paid more attention.</p>\n<p>We didn't realize it at the time, but our lives in November 1989 were \"in between\". In between jobs, in between continents, in between everything. We had just moved to Arizona, following her old boss who didn't have a job for her yet. I was working temp jobs hoping to find something permanent (one of them eventually did). We thought Arizona would become our home. We were in love with the desert, the sun, the lifestyle, everything.</p>\n<p>We had, however, picked a horrible time to move to Arizona. The savings and loan crisis had broken out, and the economy was already stagnant. And in the weeks and months that followed, the soon-to-be-reunified Germany became more attractive to us. We thought we could work there for a couple years and come back when the job climate in Arizona was better.</p>\n<p>But where was I when I heard the news? Today I have no idea. In my car listening to NPR? At home watching CNN? With my wife when she got a call from her Hungarian friend Gabi? I just know that less than 10 months later, we were here.</p>"},"next":{"fields":{"title":"Thank you to everyone for everything","date":"11 Oct 2014","slug":"/2014-10-11-thank-you-to-everyone-for-everything/"}},"previous":{"fields":{"title":"How I Punked Facebook","date":"10 Feb 2015","slug":"/2015-02-10-how-i-punked-facebook/"}}},{"node":{"fields":{"title":"Thank you to everyone for everything","date":"11 Oct 2014","slug":"/2014-10-11-thank-you-to-everyone-for-everything/","tags":"Y2014"},"html":"<p>Thanks to everyone who sent condolences for my father, be it on PapaScott, Facebook, Twitter or even with written words on paper. I debated posting publically about my feelings, and now I'm glad I did.</p>\n<p>I'm currently \"between the continents\", so to speak. I returned yesterday from the US, and fly today to Dubrovnik for 4 days on business. I still have some thoughts on the past week, the funeral, the Midwestern traditions, and the hotdish. I'll share them once I get the chance.</p>"},"next":{"fields":{"title":"Absence","date":"04 Oct 2014","slug":"/2014-10-04-absence/"}},"previous":{"fields":{"title":"Where Were You?","date":"09 Nov 2014","slug":"/2014-11-09-where-were-you/"}}}],"pathPrefix":"","first":false,"last":false,"index":12,"pageCount":1101}}

